.list-title {
  font-weight: bold;
  position: relative;
  overflow: hidden;
  word-wrap: break-word;
}

.list-title p, .list-title .textareaContainer {
  float: left;
  margin-right: 25px;
}

.list-title p {
  cursor: pointer;
}

.list-title textarea {
  margin: 0;
  padding: 0px 0px 5px 0px;
  overflow: hidden;
  width: 100%;
  height: 25px;
  outline: none;
  border: 0 none #FFF;
  resize: none;
  border-radius: 5px;
}

.list-title textarea:active {
  outline: none;
  border: 0 none #FFF;
}

.list-title .textareaContainer {
  width: -moz-calc(100% - 25px);
  width: -webkit-calc(100% - 25px);
  width: calc(100% - 25px);
}

.list-title .list-title-edit {
  width: 25px;
  position: absolute;
  right: 0px;
  padding: 5px;
  border-radius: 2.5px;
  height: 25px;
  cursor: pointer;
}

.list-title .list-title-edit:hover {
  background-color: #ccc;
}

.fade-transition {
  transition: all 0.5s ease;
}

.fade-enter, .fade-leave {
  opacity: 0;
}

.panel {
  background-color: #ecf0f1;
}

.panel-body {
  padding: 10px 10px 0px 10px;
}

.task {
  padding: 5px;
  position: relative;
  word-wrap: break-word;
  list-style: none;
  background-color: #FFFFFF;
  margin-bottom: 5px;
  text-align: left;
  padding: 5px 0px 5px 7px;
  border-radius: 3px 3px;
  cursor: pointer;
  box-shadow: 0px 1px 1px #888888;
}

.task .task-title {
  width: 100%;
  padding-right: 25px;
  font-size: 11px;
  font-weight: 500;
  margin-right: 25px;
}

.task:hover .task-edit {
  display: initial;
}

.task .task-edit {
  position: absolute;
  right: 5px;
  top: 5px;
  height: 25px;
  width: 25px;
  font-size: 10px;
  padding: 8px;
  border-radius: 2.5px;
  display: none;
  cursor: pointer;
}

.task .task-edit:hover {
  background-color: #ccc;
}

.task textarea {
  margin: 0;
  padding: 0px 0px 5px 0px;
  overflow: hidden;
  width: 100%;
  height: 25px;
  outline: none;
  border: 0 none #FFF;
  resize: none;
  border-radius: 5px;
}

.task textarea:active {
  outline: none;
  border: 0 none #FFF;
}

.list-group > li > .btn-group {
  position: absolute !important;
  right: 0px !important;
  top: 0px !important;
}

.newTaskContainer .newTask {
  padding: 10px;
}

.newTaskContainer .newTaskPlaceholder {
  padding: 10px;
}

.newTaskContainer .newTaskPlaceholder:hover {
  cursor: pointer;
  background-color: #bdc3c7;
  text-decoration: underline;
  color: #060707;
}

.newTaskContainer .newTask_remove {
  font-size: 20px;
  vertical-align: middle;
  color: #ababab;
  cursor: pointer;
}

.newTaskContainer .newTask_remove:hover {
  color: initial;
}

.ghost-task {
  background-color: #bdc3c7;
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 5px;
}

.board-title {
  cursor: default;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  color: #FFF;
  padding-left: 5px;
}

.navbar {
  margin-bottom: 5px;
}

.navbar a {
  color: #FFF;
}

.container {
  width: 100%;
}

@media screen and (min-width: 768px) {
  .navbar-brand-centered {
    position: absolute;
    left: 50%;
    display: block;
    width: 160px;
    text-align: center;
    cursor: pointer;
  }
  .navbar-brand-centered:hover {
    color: #FFF;
  }
  .navbar > .container .navbar-brand-centered,
  .navbar > .container-fluid .navbar-brand-centered {
    margin-left: -80px;
  }
}
